<template>
	<header :class="`flex-xb ${navShowFlag ? 'open' : ''}`">
		<a
			href="/home"
			class="logo pic"
		>
			<img
				:src="getLogo"
				alt="logo"
			/>
		</a>

		<div class="center">
			<slot name="title"></slot>
		</div>
		<div
			class="navbar-toggle"
			title="å¯¼èˆªï¼šç‚¹å‡»æ‰“å¼€"
			@click="switchNav"
		>
			<span class="line-top"></span>
			<span class="line-middle"></span>
			<span class="line-bottom"></span>
		</div>
		<div class="navGroup flex-yc">
			<router-link
				v-for="(item, index) in List"
				:key="index"
				@click="itemTo(item)"
				:to="item.path"
			>
				{{ item.meta.title }}
			</router-link>
		</div>
	</header>
</template>

<script>
import { routes } from '@/router/routes';
export default {
	props: {
		logoType: {
			default: 'tool',
			type: String,
		},
	},
	computed: {
		getLogo() {
			let logo = '';
			switch (this.logoType) {
				case 'blog':
					logo = require('@/assets/img/logo.svg');
					break;
				default:
					logo = require('@/assets/img/logo-tool.svg');
					break;
			}
			return logo;
		},
	},
	data() {
		return {
			navShowFlag: false,
			List: [],
		};
	},
	created() {
		this.List = routes.filter((x) => x.meta.homeShow !== false);
	},
	methods: {
		switchNav() {
			this.navShowFlag = !this.navShowFlag;
		},
	},
};
</script>
<style>
header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 10px 10px;
	background: #fff;
	z-index: 50;
	box-shadow: 0 0 5px #999;
}

header .center {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
header .center h1 {
	font-size: 18px;
}

.logo {
	height: 100%;
	font-size: 18px;
	display: block;
}

.logo img {
	width: auto;
	height: 28px;
}

.navGroup {
	position: absolute;
	right: 50px;
	top: 0%;
	height: 48px;
	opacity: 0;
	pointer-events: none;
	transition: all ease 0.3s;
	z-index: 50;
}

.open .navGroup {
	opacity: 1;
	pointer-events: visible;
}

.navGroup a {
	display: inline-block;
	margin: 0 10px;
	padding: 5px 10px;
	border-radius: 3px;
	font-size: 14px;
	background: #000;
	color: #fff;
	border: 1px solid #000;
	transition: all ease 0.2s;
}

.navGroup a:hover,
.navGroup a.nuxt-link-exact-active {
	opacity: 0.7;
}

.navbar-toggle {
	margin-left: 20px;
}
</style>
